<template>
  <el-container>
    <el-header style="height: 5%"></el-header>
    <el-main style="display: flex; flex-direction: column">
      <div>
        <el-dialog title="新增人员" :visible.sync=" addview ">
          <el-form :model="form">
            <el-form-item label="姓名" style="width: 60%; font-size: 4vw">
              <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" style="width: 60%; font-size: 4vw">
              <el-input v-model="form.password" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="quxiao">取 消</el-button>
            <el-button type="primary" @click="add">确 定</el-button>
          </div>
        </el-dialog>

        <el-table
          :data="tableData"
          style="width: 100%;height: 50vh; font-size: 2vh"
          height="200"
        >
          <el-table-column fixed="left" prop="username" label="姓名" width="200">
          </el-table-column>
          <el-table-column fixed prop="password" label="密码" width="200">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="200">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="delet(scope.row.username)"
                type="text"
                size="small"
                >移除</el-button
              >
            </template>
          </el-table-column>
        </el-table>

        <el-button type="success" @click="open" style="font-size: 3vh">增加</el-button>
        <el-button type="warning" @click="close" style="font-size: 3vh">退出登录</el-button>

      </div>
      <div style="height: 10vh"></div>
    </el-main>
  </el-container>
</template>


  <script>
import "@/router/index";
import axios from "@/untils/axios";
export default {
  data() {
   
    return {
      
      form: {},
      tableData: [],

      addview:false
      
    };
  },
  methods: {
    close() {
    // 清空本地存储中的 token 值
    window.localStorage.removeItem("token");
    this.$router.push("/index");
    // 其他你想要执行的操作可以在这里添加
},

    quxiao() {
      this.addview = false;
      this.form = {};
    },
    open() {
      
      this.addview = true;
      
    },

    fetchData() {
      axios.get("http://localhost:8081/User/selectAll").then((res) => {
        console.log(res);
        this.tableData = res.data.data;
      });
    },
    add() {
      // 要发送的数据对象
      const data = {
        username: this.form.name,
        password: this.form.password,
      };

      // 发送 POST 请求
      axios
        .post("http://localhost:8081/User/insert", data)
        .then((response) => {
          // 请求成功的处理逻辑
          console.log("添加用户成功:", response.data);
          // 如果有返回值再进行相应提示
          if (response.data) {
            // 可以根据响应结果进行进一步的处理
            // 示例：提示用户添加成功
            alert("用户添加成功");
      this.form = {};

            location.reload();
          } else {
            // 如果没有返回值，说明添加失败
            console.error("添加用户失败");
            alert("用户添加失败");
          }
        })
        .catch((error) => {
          // 请求失败的处理逻辑
          console.error("添加用户时出错:", error);
          // 可以根据错误信息进行进一步的处理
        });
    },
    delet(username) {
      // 构建要发送的数据对象
      const data = {
        username: username,
      };

      // 发送 DELETE 请求
      axios
        .delete("http://localhost:8081/User/delete", { params: data })
        .then((response) => {
          // 请求成功的处理逻辑
          if (response.data.code === 1) {
            console.log("删除成功:", response.data);
            location.reload(); 
          }
        })
        .catch((error) => {
          // 请求失败的处理逻辑
          console.error("删除用户时出错:", error);
          // 可以根据错误信息进行进一步的处理
        });
    },
  },
  mounted() {
    this.fetchData();
  },
};
</script>
  <style>
.el-container {
  display: flex;
  flex-direction: column;
  height: 100vh; /* 使用视口高度作为容器高度 */
}

.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
  background-image: url("./标签栏.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  /* flex: 1; 
  display: flex;
  justify-content: center;
  align-items: center; */
  background-image: url("./内容区底图.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.el-menu-item {
  margin-bottom: 20px; /* 设置菜单项之间的底部外边距 */
}
.custom-form-item {
  font-size: 60px; /* 修改表单项文字的大小 */
}
.el-form-ite_lable {
  font-size: 30px;
}
</style>

